@import './_variables';
@import './_buttons';
@import './_icons';

.url-inputs {
  height: $input-height;
  padding: $distance-s;
  line-height: $distance-s;
  border: none;
  box-shadow: none;
  border-radius: 0;
}
.url-form-inputs {
  @extend .url-inputs;
  background-color: $input-bgr-color;
  color: $input-color;
  font-size: $input-font-height;
  font-weight: $input-font-weight;
}
.url-form-inputs-focus {
  background: #FFF;
  border: 1px solid $input-color;
  box-shadow: none;
}
.url-button {
  @extend .url-inputs;
  background-color: $button-bgr-color;
  border: $button-border;
  color: $button-color;
  padding: 0 $distance-xl;
  text-transform: uppercase;
  font-size: $input-font-height - 1px;
}

form {
  input, textarea {
    @extend .url-form-inputs;
    &:focus {
      @extend .url-form-inputs-focus;
    }
  }
  input {
    &[type="checkbox"], &[type="radio"] {
      height: auto;
    }
    &[type="button"] {
      @extend .url-button;
    }
  }
  
  textarea {
    height: 2*$input-height;
  }

  button {
    // @extend .url-button;
    @extend .af-button-streatched;  
  }
  
  .has-error {
    position: relative;
    .alert {
      position: absolute;
      font-size: 12px;
      top: -$distance-s;
      border: none;
      padding: 0;
      &.alert-danger {
        color: $form-error-color !important;
        background-color: #FFF;
      }
    }
    * {
      color: $form-error-color;
    }
    input {
      border: 1px solid $form-error-color;
    }
  } 
}

a, label, .link-alike {
  color: $links-color;
  text-transform: uppercase;
  font-size: $input-font-height;
  font-weight: bold;
}
a {
  text-decoration: underline;
  font-weight: bold;
}
label {
  font-weight: $input-font-weight;
}

.url-heading {
  margin-bottom: $distance-xl;
  .url-heading-icon {
    margin-bottom: $distance-s;
  }
  .url-heading-title {
    font-weight: normal;
    font-size: 22px;
  }
}

.form-group {
  margin-bottom: $distance-sb;
  // &:last-child {
  //   margin-bottom: $distance-s;
  // }
}
.forgot-password-group {
  margin-bottom: $distance-l;
}
.signin-link {
  margin-bottom: $distance-m;
}
.actions-group {
  margin-bottom: $distance-s;
}

.url-centered {
  margin: 0 auto;
  width: 50%;
}

.select2-container--default {
  .select2-selection--single {
    background-color: #fff;
    border: none;
    border-radius: 0;
    height: auto;
    
    .select2-selection__rendered, .select2-selection__arrow {
      @extend .url-form-inputs;
      background-color: transparent;
      border: 1px solid $input-color;
      outline: none;
      &.select2-selection__arrow {
        background-color: transparent;
        border: none;
        top: 0;
        right: 0;
        @extend .af-icon;
        @extend .icon-arrow-down;
        // TODO: Here icon paths are not OK so we have to override as below
        background-image: url('../img/icons/arrow_down_gray.png');
        background-size: $distance-s;
        height: 100%;
        b {
          display: none;
        }
      }
    }
    &:hover {
      .select2-selection__arrow {
        background-color: transparent;
        border: none;
        top: 0;
        right: 0;
        @extend .af-icon;
        @extend .icon-arrow-down-over;
        // TODO: Here icon paths are not OK so we have to override as below
        background-image: url('../img/icons/arrow_down.png');
        background-size: $distance-s;
        height: 100%;
      }
    }
  }
  // Opened state
  &.select2-container--open {
    .select2-selection--single {
      .select2-selection__rendered {
        @extend .url-form-inputs-focus;
      }
      .select2-selection__arrow {
        @extend .icon-arrow-up;
        // TODO: Here icon paths are not OK so we have to override as below
        background-image: url('../img/icons/arrow_up.png');
      } 
    }
  }
}

