@import './_variables';

$xs-tickness: $distance-s - ($distance-s/4);
$xs-tickness-lr: $distance-m - ($distance-m/4);
.af-thickness-xs {
  padding: $xs-tickness $xs-tickness-lr;
}
.af-thickness-s {
  padding: $distance-s $distance-m;
}

.af-button-base {
  @extend .af-thickness-xs;
  display: inline-block;
  text-decoration: none;
  background: $color-white;
  color: $color-black;
  border-color: $color-black;
  border-style: solid;
  border-top-width: 1px;
  border-right-width: 1px;
  border-bottom-width: 1px;
  border-left-width: 1px;
  border-radius: 0;
  &:hover {
    background: $color-black;
    color: $color-white;
    border-color: $color-white;
    text-decoration: none;
  }
}

.af-button {
  display: inline-block;
  background: $color-black;
  color: $color-white;
  text-decoration: none;
  @extend .af-thickness-xs;
  
  &.thickness-s {
    @extend .af-thickness-s;
  }
}

.af-btn, .af-btn-dashed {
  @extend .af-button-base;
  &.thickness-s {
    @extend .af-thickness-s;
  }
  &.af-btn-dashed {
    color: $color-gray-4;
    border-color: $color-gray-4;
    border-style: dashed;
    &:hover {
      background-color: transparent;
      color: $color-black;
      border-color: $color-black;
    }
    .af-icon {
      margin-right: $distance-xxs;
    }
  }
}

.af-button-streatched {
  @extend .af-button-base;
  padding: $distance-s $distance-l;
}