@import './_variables';

.af-icon {
  display: inline-block;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100%;
  width: 30px;
  height: 30px;
  vertical-align: middle;
}

.af-icon-xs {
  width: $distance-s - ($distance-s/4);
  height: $distance-s - ($distance-s/4);
}

.af-icon-s {
  width: $distance-s;
  height: $distance-s;
}

.af-icon-m {
  width: 25px;
  height: 25px;
}

.af-icon-l {
  width: 30px;
  height: 30px;
}
.af-icon-xl {
  width: $distance-m;
  height: $distance-m;
}
.af-icon-xxl {
  width: $distance-xxl;
  height: $distance-xxl;
}

.af-icon-xxxl {
  width: $distance-xxxl;
  height: $distance-xxxl;
}

