a, button {
  .icon-close {
    @extend .icon-close;
  }
  .icon-endorse {
    @extend .icon-endorse;
  }
  .icon-connect {
    @extend .icon-connect;
  }
  .icon-reply {
    @extend .icon-reply;
  }
  .icon-arrow-down {
    @extend .icon-arrow-down
  }
  .icon-arrow-up {
    @extend .icon-arrow-up
  }
  .icon-available-space {
    @extend .icon-available-space;
  }
  .icon-jobpost {
    @extend .icon-jobpost;
  }
  .icon-add {
    @extend .icon-add;
  }
  .icon-stapler {
    @extend .icon-stapler;
  }

  &:hover {
    .icon-close {
      @extend .icon-close-over;
    }
    .icon-close-white {
      @extend .icon-close-white-over;
    }
    .icon-endorse {
      @extend .icon-endorse-over;
    }
    .icon-connect {
      @extend .icon-connect-over;
    }
    .icon-reply {
      @extend .icon-reply-over;
    }
    .icon-arrow-down {
      @extend .icon-arrow-down-over;
    }
    .icon-arrow-up {
      @extend .icon-arrow-up-over;
    }
    .icon-available-space {
      @extend .icon-available-space-over;
    }
    .icon-jobpost {
      @extend .icon-jobpost-over;
    }
    .icon-add {
      @extend .icon-add-over;
    }
  }
  &.active, &:active {
    .icon-arrow-down {
      @extend .icon-arrow-down-active;
    }
    .icon-endorse {
      @extend .icon-endorse-active;
    }
    .icon-connect {
      @extend .icon-connect-active;
    }
    .icon-reply {
      @extend .icon-reply-active;
    }
    .icon-available-space {
      @extend .icon-available-space-active;
    }
    .icon-jobpost {
      @extend .icon-jobpost-active;
    }
    .icon-notification {
      @extend .icon-notification-active;
    }
    .icon-settings {
      @extend .icon-settings-active;
    }
    .icon-add {
      @extend .icon-add-active;
    }
  }
  &.af-btn:hover {
    .icon-arrow-left {
      @extend .icon-arrow-left-white;
    }
  }
}
