.af-icon-cnt {
  margin: 0;
  padding: 0;
  border-radius: 50%;
  display: inline-block;
  &.circle {
    border-radius: 50%;
  }
  &.white {
    background-color: $color-white;
  }
  &.black {
    background-color: $color-black;
  }
  &.transparent {
    background-color: transparent;
  }
}
